<template>
  <nav class="navbar navbar-default">
    <div class="container-fluid">
      <div class="navbar-header">
        <router-link to="/" class="navbar-brand">Stock Trader</router-link>
      </div>

       <!-- Collapse button -->
      <button class="navbar-toggler toggler-example pull-right" type="button" data-toggle="collapse" data-target="#navbarSupportedContent1"
        aria-controls="navbarSupportedContent1" aria-expanded="false" aria-label="Toggle navigation">
        <span>&#9776;</span>
      </button>

      <div class="collapse navbar-collapse" id="navbarSupportedContent1">   
        <ul class="nav navbar-nav">   
          <router-link to="/portfolio" tag="li"  activeClass="active"><a>Portfolio</a></router-link>

          <router-link to="/stocks" tag="li"  activeClass="active"><a>Stocks</a></router-link>

        </ul>

        <strong class="navbar-text navbar-right">Funds: {{funds | currency}}</strong>

        <ul class="nav navbar-nav navbar-right">
          <li class="nav-item"><a href="#" @click="endDay">End Day</a></li>
          <li class="dropdown">
            <a
              href="#"
              class="dropdown-toggle"
              id="dropdownMenuButton"
              type="button"
              data-toggle="dropdown"
              role="button"
              aria-haspopup="true"
              aria-expanded="false">Save & Load 
              <span class="caret"></span>
            </a>
            <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton">
              <li class="dropdown-item"><a href="#" @click="saveData">Save Data</a></li>
              <li class="dropdown-item"><a href="#" @click="loadData">Load Data</a></li>
            </ul>
          </li>
        </ul>

      </div>
    </div>
  </nav>
</template>

<script>
import {mapActions} from 'vuex'
export default {
  computed: {
   funds(){
     return this.$store.getters.funds
   }
  },

  methods: {
    ...mapActions({
      randomizeStocks: 'randomizeStocks',
      fetchData: 'loadData'
    }),

    endDay(){
      this.randomizeStocks()
    },

    saveData() {    
      const data = {
        funds: this.$store.getters.funds,
        stockPortfolio: this.$store.getters.stockPortfolio,
        stocks: this.$store.getters.stocks
      }
      this.$http.put('data.json', data)
    },

    loadData() {
      this.fetchData()
    },
  },
}
</script>

